import request from '../request'


// 获取列表
export function listMailbox(data) {
    return request({
        url: '/api/mailbox/listMailbox',
        method: 'post',
        data
    })
}

// 新增
export function saveMailbox(data) {
    return request({
        url: '/api/mailbox/saveMailbox',
        method: 'post',
        data
    })
}

// 修改
export function updateMailbox(data) {
    return request({
        url: '/api/mailbox/updateMailbox',
        method: 'post',
        data
    })
}

// 删除
export function deleteMailboxById(params) {
    return request({
        url: '/api/mailbox/deleteMailboxById',
        method: 'get',
        params: params
    });
}

// 修改邮箱的地区是否默认
export function updateMailboxAanIsDefault(data) {
    return request({
        url: '/api/mailbox/updateMailboxAanIsDefault',
        method: 'post',
        data
    })
}
