<template>
  <div id="style-operate">
    <!-- Form -->
    <el-dialog v-model="dialogFormVisible" :before-close="beforeClose" :title="titleName" width="600" draggable>
      <!-- Form 自定义表单 -->
      <el-form :rules="rules" ref="ruleForm" :model="form" :label-width="formLabelWidth">
        <el-form-item :label="$t('MailView.buyDitchName')" prop="ditchName" :label-width="formLabelWidth">
          <el-select filterable v-model="form.ditchName" :placeholder="$t('MailView.buyDitchNamePlaceholder')">
            <el-option v-for="item in pullDownData.nameList" :key="item.dictValue" :label="item.dictKey" :value="item.dictValue"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('MailView.isDefault')" :label-width="formLabelWidth">
          <el-radio-group v-model="form.isDefault">
            <el-radio :label="1">{{ $t('pullDown.isDefault.defaultMail') }}</el-radio>
            <el-radio :label="2">{{ $t('pullDown.isDefault.notDefaultMail') }}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item :label="$t('MailView.host')" prop="host" :label-width="formLabelWidth">
          <el-input v-model="form.host" :placeholder="$t('MailView.hostPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('MailView.username')" prop="username" :label-width="formLabelWidth">
          <el-input v-model="form.username" :placeholder="$t('MailView.usernamePlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('MailView.password')" prop="password" :label-width="formLabelWidth">
          <el-input v-model="form.password" :placeholder="$t('MailView.passwordPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('MailView.port')" prop="port" :label-width="formLabelWidth">
          <el-input-number v-model="form.port" :placeholder="$t('MailView.portPlaceholder')" :readonly="isReadonly" class="mx-4" :step="10" :min="0" controls-position="right" />
        </el-form-item>
        <el-form-item :label="$t('MailView.protocol')" prop="protocol" :label-width="formLabelWidth">
          <el-input v-model="form.protocol" :placeholder="$t('MailView.protocolPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('MailView.defaultEncoding')" prop="defaultEncoding" :label-width="formLabelWidth">
          <el-input v-model="form.defaultEncoding" :placeholder="$t('MailView.defaultEncodingPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('MailView.remarks')" :label-width="formLabelWidth">
          <el-input type="textarea" v-model="form.remarks" :placeholder="$t('MailView.remarksPlaceholder')" :readonly="isReadonly" style="width: 68%"></el-input>
        </el-form-item>
      </el-form>

      <template #footer>
      <span class="dialog-footer">
        <!-- Form 按钮 -->
        <el-button @click="closeButton()">
          {{ $t('common.button.cancel') }}
        </el-button>
        <el-button type="primary" v-show="type != 'detail'" @click="submitButton()"> {{ $t('common.button.confirm') }} </el-button>
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import {ElNotification} from "element-plus";
import {getRegionPullDown} from "@/http/clienteleApi";
import {saveMailbox, updateMailbox} from "@/http/clientPage/mailboxApi";
import {getDictPullDown} from "@/http/PullDownApi";

export default {
  name: "style-operate",
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      // 控制对话框大开闭
      dialogFormVisible: false,
      // 联动列表数据 - 行数据副本
      rowData: {},
      form: {
        isDefault: 2,
        port: 25,
        protocol: 'smtp',
        defaultEncoding: 'UTF-8',
      },
      // 表单验证规则
      rules: {
        ditchName: [
          {required: true, message: this.$t('MailView.buyDitchNamePlaceholder'), trigger: 'change'},
        ],
        host: [
          {required: true, message: this.$t('MailView.hostPlaceholder'), trigger: 'blur'},
        ],
        username: [
          {required: true, message: this.$t('MailView.usernamePlaceholder'), trigger: 'blur'},
        ],
        password: [
          {required: true, message: this.$t('MailView.passwordPlaceholder'), trigger: 'blur'},
        ],
        port: [
          {required: true, message: this.$t('MailView.portPlaceholder'), trigger: 'blur'},
        ],
        protocol: [
          {required: true, message: this.$t('MailView.protocolPlaceholder'), trigger: 'blur'},
        ],
        defaultEncoding: [
          {required: true, message: this.$t('MailView.defaultEncodingPlaceholder'), trigger: 'blur'},
        ],
      },
      // 进入类型: 新增 详情 编辑
      type: '',
      titleName: this.$t('common.dialog.defaultTitle'),
      // 表单的只读
      isReadonly: false,
      formLabelWidth: '120px',
      pullDownData: {}, // 查询框数据集合对象
    }
  },

  // watch 侦听器 能够侦听到data数据的变化 且获取对应的 新值 和 旧值
  watch: {
    type(newVal, oldVal) {
        if (newVal == '') return
        if (newVal == 'add') this.titleName = this.$t('common.dialog.addTitle')
        if (newVal == 'edit') this.titleName = this.$t('common.dialog.editTitle')
        if (newVal == 'detail') this.titleName = this.$t('common.dialog.detailTitle')
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {

  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    selectData() {
      // 地区 暂测
      getRegionPullDown().then(res => {
        if (res.data.status == 200) this.pullDownData.regionList = res.data.data;
      }).catch((erro) => { });
      // 邮箱名称
      getDictPullDown({dictCode: 'HFYX'}).then(res => {
        if (res.data.status == 200) this.pullDownData.nameList = res.data.data;
      }).catch((erro) => { });
    },
    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.dialogFormVisible= false;
      this.isReadonly= false;
      this.form= {
        isDefault: 2,
        port: 25,
        protocol: 'smtp',
        defaultEncoding: 'UTF-8',
      };
      this.rowData= {};
      this.$refs.ruleForm.resetFields(); // 重置表单验证
    },
    // 确认按钮
    submitButton() {
      // 提交校验
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          if (this.type == "add") {
            saveMailbox(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible= false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => { });
          } else if (this.type == "edit") {
            updateMailbox(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible= false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => { });
          }
          this.closeButton();
        } else {
          return false;
        }
      });
    }
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.el-button--text {
  margin-right: 15px;
}
.el-select {
  width: 300px;
}
.el-input {
  width: 300px;
}
.dialog-footer button:first-child {
  margin-right: 10px;
}
.text-center {
  text-align: center;
}
/* 数字输入框 */
/deep/ .el-input-number .el-input__inner {
  text-align: left;
}
</style>
