<template>
  <div class="moduleBoxView">
    <div class="moduleFrom">
      <el-form :inline="true" :model="fromData" class="demo-form-inline" :label-width="80" label-position="right">

        <el-form-item :label="$t('MailView.buyDitchName')">
          <el-select filterable v-model="fromData.ditchName" :placeholder="$t('MailView.buyDitchNamePlaceholder')">
            <el-option v-for="item in selectData.nameList" :key="item.dictValue" :label="item.dictKey" :value="item.dictValue"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('MailView.username')"><el-input v-model="fromData.username" :placeholder="$t('MailView.usernamePlaceholder')" clearable></el-input></el-form-item>
        <el-form-item>
          <el-button type="primary" @click="listData()" icon="Search"> {{ $t('common.button.search') }} </el-button>
          <el-button type="info" @click="reset()" icon="Loading">  {{ $t('common.button.reset') }} </el-button>
          <el-button type="success" v-show="isButton('series.add')" @click="add()"> {{ $t('common.button.add') }} </el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="moduletTable" ref="tableDiv">
      <el-table :data="tableData" :height="tabHeight" stripe border fit highlight-current-row :empty-text="$t('common.table.noData')" style="width: 100%; margin-bottom: 5px;" >
        <el-table-column fixed type="index" :label="$t('common.table.number')" width="90"/>
        <el-table-column prop="buyDitchName" :label="$t('MailView.buyDitchName')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="host" :label="$t('MailView.host')" width="200" show-overflow-tooltip></el-table-column>
        <el-table-column prop="username" :label="$t('MailView.username')" width="200" show-overflow-tooltip></el-table-column>
        <el-table-column prop="password" :label="$t('MailView.password')" width="200" show-overflow-tooltip></el-table-column>
        <el-table-column prop="port" :label="$t('MailView.port')" width="80" show-overflow-tooltip></el-table-column>
        <el-table-column prop="protocol" :label="$t('MailView.protocol')" width="120" show-overflow-tooltip></el-table-column>
        <el-table-column prop="defaultEncoding" :label="$t('MailView.defaultEncoding')" width="120" show-overflow-tooltip></el-table-column>
        <el-table-column prop="remarks" :label="$t('MailView.remarks')" width="250" show-overflow-tooltip></el-table-column>
        <el-table-column prop="createUserName" :label="$t('MailView.createUser')" width="120" show-overflow-tooltip></el-table-column>
        <el-table-column prop="createTime" :label="$t('MailView.createTime')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="isDefault" :label="$t('MailView.isDefault')" fixed="right" width="120">
          <template v-slot="{ row, column, $index }">
            <el-switch v-model="row.isDefault" class="ml-2" inline-prompt
                       style="--el-switch-on-color: #13ce66; --el-switch-off-color: #ff4949"
                       :active-value=1 :active-text="$t('pullDown.isDefault.defaultMail')"
                       :inactive-value=2 :inactive-text="$t('pullDown.isDefault.notDefaultMail')" @change="handleSwitchChange(row)"
            />
          </template>
        </el-table-column>
        <el-table-column fixed="right" :label="$t('common.table.operation')" width="300">
          <template #default="scope">
            <el-button type="primary" size="small" @click="goOperate(scope.$index, scope.row, 'detail')"> {{ $t('common.button.detail') }} </el-button>
            <el-button type="warning" size="small" @click="goOperate(scope.$index, scope.row, 'edit')" v-show="isButton('series.update')"> {{ $t('common.button.edit') }} </el-button>
            <el-button type="danger" size="small" @click="deleteRow(scope.$index, scope.row)" v-show="isButton('series.delete')">  {{ $t('common.button.delete') }}  </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>


    <div class="modulePagination">
      <el-pagination
          background
          v-model:current-page="pageNum"
          v-model:page-size="pageSize"
          :page-sizes="[10, 30, 50, 75, 100]"
          :small="false"
          layout="prev, pager, next, sizes, jumper, total"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
    <mailbox-operate ref="operate" @operateClick="listData"/>
  </div>
</template>

<script>
import {resourceIsUser} from "@/util/resource";
import {getDictPullDown, getRolePullDown} from "@/http/PullDownApi";
import {ElLoading, ElNotification} from "element-plus";
import {deleteMailboxById, listMailbox, updateMailboxAanIsDefault} from "@/http/clientPage/mailboxApi";
import mailboxOperate from "@/views/clientPage/mailbox/mailbox-operate";
import {getRegionPullDown} from "@/http/clienteleApi";

export default {
  name: "message",
  components: {mailboxOperate},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      tabHeight: 0, // 表格高度
      selectData: {}, // 查询框数据集合对象
      fromData: {}, // 查询条件

      tableData: [],
      total: 0,           // 总条数
      pages: 0,           // 总页数
      pageNum: 1,         //当前页码
      pageSize: 30,        // 每页条数
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    this.getSelectData();
    this.listData();
    this.refreshTableHeight();
  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    // 刷新表格高度
    refreshTableHeight() {
      // 创建一个 ResizeObserver 实例
      const resizeObserver = new ResizeObserver(entries => {
        // entries 是 ResizeObserverEntry 对象的数组，包含观察到的元素信息
        for (const entry of entries) {
          if (entry.target === this.$refs.tableDiv) {
            // 如果观察到的元素是你要监听的元素
            // console.log('窗口大小改变，新的表格高度：', entry.contentRect.height);
            this.tabHeight = this.$refs.tableDiv.offsetHeight - 20;
          }
        }
      });
      // 将 ResizeObserver 注册到要观察的元素
      resizeObserver.observe(this.$refs.tableDiv);
    },
    // 获取按钮权限
    isButton(symbolStr) {
      return resourceIsUser(symbolStr);
    },

    // 获取查询条件数据
    getSelectData() {
      // 获取角色的数据
      getRolePullDown().then(res => {
        if (res.data.status == 200) this.selectData.roleList = res.data.data;
      }).catch((erro) => { });
      // 地区 暂测
      getRegionPullDown().then(res => {
        if (res.data.status == 200) this.selectData.regionList = res.data.data;
      }).catch((erro) => { });
      // 邮箱名称
      getDictPullDown({dictCode: 'HFYX'}).then(res => {
        if (res.data.status == 200) this.selectData.nameList = res.data.data;
      }).catch((erro) => { });
    },
    // 列表数据请求
    listData() {
      const loading = ElLoading.service({
        lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
      );
      this.fromData.pageSize = this.pageSize;
      this.fromData.pageNum = this.pageNum;
      listMailbox(this.fromData).then(res => {
        this.tableData = res.data.data.list;
        // 给分页组件的数据赋值
        this.total = res.data.data.total;
        this.pages = res.data.data.pages;
        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
      }).catch((erro) => {
        console.log(erro, 'erro')
      }).finally(() => {
        // 请求无论成功/失败 都执行关闭loading
        loading.close();
      });
    },
    // 设置每页条数方法 val=条数
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageNum = 1;                       // 修改当前页为第1页
      this.pageSize = val;               // 修改每页条数
      this.listData();
    },
    // 点击 前一页、后一页、页码 事件
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.pageNum = val;                 // 修改当前页码
      this.listData();
    },
    // 重置按钮
    reset() {
      this.fromData = {};
      this.listData();
    },
    handleSwitchChange(row) {
      if (row.isDefault == 2) {
        ElNotification({
          title: this.$t('common.message.tipsTitle'),
          message: this.$t('common.message.isDefault'),
        })
        // 延迟1秒执行
        setTimeout(() => {
          row.isDefault = 1;
        }, 500)
        return;
      } else {
        // 后台请求修改数据库数据
        updateMailboxAanIsDefault(row).then(res => {
          if (res.data.status == 200) {
            ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
            this.listData();
          }
        }).catch(error => { });
      }
    },
    // 新增
    add() {
      this.$refs.operate.dialogFormVisible = true;
      this.$refs.operate.type = "add";
      this.$refs.operate.selectData(); // 初始化下拉框数据
    },
    // 详情 / 编辑 执行方法
    goOperate(index, row, type) {
      this.$refs.operate.dialogFormVisible = true;
      this.$refs.operate.type = type;
      this.$refs.operate.selectData(); // 初始化下拉框数据
      if (type == "detail") {
        // 开启只读 | 深拷贝创建了目标对象的一个完整副本
        this.$refs.operate.isReadonly = true;
        this.$refs.operate.form = JSON.parse(JSON.stringify(row));
      } else if (type == "edit") {
        this.$refs.operate.rowData = row;
        this.$refs.operate.form = JSON.parse(JSON.stringify(row));
      }
    },
    // 删除
    deleteRow(index, row) {
      this.$confirm(this.$t('common.dialog.deleteTips') , this.$t('common.dialog.deleteTitle'), {
        confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'warning'
      }).then(() => {
        // 后台请求删除数据库数据
        deleteMailboxById({ id: row.id}).then(res => {
          if (res.data.status == 200) {
            // 删除前端渲染数组的数据
            this.tableData.splice(index, 1);
            ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
          }
        }).catch(error => { });
      })
    },
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
/*导航小标题*/
.el-breadcrumb {
  padding: 0px 0px 20px 0px;
}


